import { createStore } from "vuex";
import createPersistedState from "vuex-persistedstate";

export default createStore({
  state: {
    account: "",
  },
  mutations: {
    UPDATE_ACCOUNT: function (state, account) {
      state.account = account;
      // console.log(account);
      // state.account = "0x5A15693189D1CCD50DFEA7282b3E8cEcc4B86087";
    },
  },
  actions: {},
  modules: {},
  getters: {
    shortAccount(state) {
      if (state.account) {
        return (
          state.account.substring(0, 6) +
          "..." +
          state.account.substring(37, 42)
        );
      }
      return null;
    },
  },
  plugins: [createPersistedState()],
});
