<template>
  <div class="mt-3 md:mt-10 md:p-2">
    <p class="md:font-medium md:text-lg">Stake {{ pool.lpName }} token to earn SPOON rewards</p>
    <p v-if="pool.subTitle" v-html="pool.subTitle" class="mt-1 text-sm text-gray-700"></p>
    <div @click="isShowAction = !isShowAction" class="border-2 p-2 md:p-5 mt-2">
      <div class="grid grid-cols-10 text-xs md:font-bold md:text-lg">
        <div class="col-span-2">
          <img class="w-6 md:w-12 inline z-40" :src="pool.token0src" />
          <img class="w-6 md:w-12 inline z-0 -ml-3 md:-ml-4" :src="pool.token1src" />
        </div>
        <div class="col-span-2 md:col-span-3 m-auto">{{ pool.lpName }}</div>
        <div class="col-span-3 md:col-span-2 m-auto">
          <span
            class="rounded md:rounded-xl bg-green-400 text-white p-1 md:p-2"
          >APY {{ formatBN(apy, { decimals: 0, dp: 2 }) }}%</span>
        </div>
        <div class="col-span-2 m-auto">SPOON Earned: {{ formatBN(rewards, {dp:3}) }}</div>
        <div class="col-span-1 m-auto font-normal text-gray-500">
          <i v-show="isShowAction" class="icon ion-chevron-down"></i>
          <i v-show="!isShowAction" class="icon ion-chevron-right"></i>
        </div>
      </div>
    </div>
    <div v-if="isShowAction" class="border-2 border-t-0 p-1 md:p-5 bg-gray-100">
      <div class="flex-row md:grid md:grid-cols-11 text-gray-400">
        <div class="col-span-4 p-3">
          <p class="font-bold">I want to stake</p>
          <p>Available {{ pool.lpName }} token Balance: {{ balance }}</p>
          <div class="pt-2 pb-2">
            <input
              :disabled="needApprove || balance == '0.0'"
              v-model="stakeAmount"
              class="text-xl pl-1 w-9/12 text-green-400 border-2 border-r-0"
              type="number"
            />
            <button
              @click="stakeAmount = balance"
              class="text-xl text-green-400 border-2 w-3/12"
            >Max</button>
          </div>
          <Button v-if="needApprove" @click="onApprove" class="w-full" :loading="approving">Approve</Button>
          <Button
            v-else
            :disabled="balance == '0.0'"
            @click="onStake"
            class="w-full"
            :loading="staking"
          >Stake</Button>
        </div>
        <div class="col-span-4 p-3">
          <p class="font-bold">I want to unstake</p>
          <p>Staked {{ pool.lpName }} token Balance: {{ staked }}</p>
          <div class="pt-2 pb-2">
            <input
              v-model="withdrawAmount"
              :disabled="staked == '0.0'"
              class="text-xl pl-1 w-9/12 text-green-400 border-2 border-r-0"
              type="number"
            />
            <button
              @click="withdrawAmount = staked"
              class="text-xl text-green-400 border-2 w-3/12"
            >Max</button>
          </div>
          <Button
            @click="onUnstake"
            :disabled="staked == '0.0'"
            class="w-full"
            :loading="unstaking"
          >Unstake</Button>
        </div>
        <div class="col-span-3 md:pl-4 md:border-l-2 text-center">
          <p>Total SPOON rewards:</p>
          <p class="text-3xl text-green-400 pt-6 pb-6">{{ formatBN(rewards, {dp:3}) }}</p>
          <Button @click="onClaim" class="w-full" :loading="claiming">Claim</Button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Button from "./Button";
import {
  getFairLaunchContract,
  getProvider,
  getContract,
} from "@/utils/initWeb3";
import { ethers } from "ethers";
import PancakeSwapABI from "@/assets/abi/PancakeSwap.json";
import ERC20 from "@/assets/abi/ERC20.json";
import vaultABI from "@/assets/abi/vault.json";
import lpABI from "@/assets/abi/LP.json";
import BigNumber from "bignumber.js";
import {
  stakePools,
  fairLaunchAddress,
  usdAddress,
  pancakeSwapRouter,
  wftmAddress,
  akitaAddress,
  usdDecimals,
} from "@/config";
import ibTokenABI from "@/assets/abi/ibToken.json";
export default {
  components: {
    Button,
  },
  props: {
    pool: null,
    totalAllocPoint: null,
  },
  data() {
    return {
      tokenPrice: null,
      needApprove: false,
      approving: false,
      isShowAction: false,
      staking: false,
      unstaking: false,
      claiming: false,
      apy: "...",
      balance: "0",
      staked: "0",
      rewards: "0",
      stakeAmount: "0",
      withdrawAmount: "0",
      stakeToken: null,
      poolInfo: null,
    };
  },
  watch: {
    "$store.state.account": async function (account) {
      if (account) {
        this.tokenPrice = await this.getTokenPrice(
          [akitaAddress, wftmAddress, usdAddress],
          18,
          usdDecimals
        );
        this.loadData();
      }
    },
  },
  async mounted() {
    if (this.$store.state.account) {
      this.tokenPrice = await this.getTokenPrice(
        [akitaAddress, wftmAddress, usdAddress],
        18,
        usdDecimals
      );
      this.loadData();
    }
  },
  methods: {
    async getTokenPrice(path, inDecimals, outDecimals) {
      try {
        const pancakeSwap = new ethers.Contract(
          pancakeSwapRouter,
          PancakeSwapABI,
          getProvider()
        );
        let tokenPrice = await pancakeSwap.getAmountsOut(
          ethers.utils.parseUnits("1", inDecimals),
          path
        );
        tokenPrice = new BigNumber(
          tokenPrice[tokenPrice.length - 1].toString()
        ).dividedBy(new BigNumber(10).pow(outDecimals));
        return tokenPrice;
      } catch (error) {
        console.log("getTokenPrice", path, error);
        return "0";
      }
    },
    async onApprove() {
      this.approving = true;
      try {
        const tx = await this.stakeToken.approve(
          fairLaunchAddress,
          ethers.BigNumber.from("2").pow("256").sub("1")
        );
        await tx.wait();
        this.needApprove = false;
      } catch (error) {
        console.log("onApprove", error);
      } finally {
        this.approving = false;
      }
    },
    async onStake() {
      this.staking = true;
      try {
        const tx = await getFairLaunchContract().deposit(
          this.$store.state.account,
          this.pool.id,
          ethers.utils.parseUnits(this.stakeAmount, this.pool.decimals)
        );
        await tx.wait();
        this.loadData();
      } catch (error) {
        console.log("onStake", error);
      } finally {
        this.staking = false;
      }
    },
    async onUnstake() {
      this.unstaking = true;
      try {
        const tx = await getFairLaunchContract().withdraw(
          this.$store.state.account,
          this.pool.id,
          ethers.utils.parseUnits(this.withdrawAmount, this.pool.decimals)
        );
        await tx.wait();
        this.loadData();
      } catch (error) {
        console.log("onUnstake", error);
      } finally {
        this.unstaking = false;
      }
    },
    async onClaim() {
      this.claiming = true;
      try {
        const tx = await getFairLaunchContract().harvest(this.pool.id);
        await tx.wait();
        this.loadData();
      } catch (error) {
        console.log("onClaim", error);
      } finally {
        this.claiming = false;
      }
    },
    async calcAPY() {
      try {
        const yearlyUSDReward = new BigNumber(365)
          .multipliedBy(
            (await getFairLaunchContract().getCurRewardRate()).toString()
            // 100000000000000000000000
          )
          .multipliedBy(this.tokenPrice)
          .multipliedBy(this.poolInfo.allocPoint.toString())
          .dividedBy(this.totalAllocPoint.toString())
          .dividedBy(new BigNumber(10).pow(18));
        let totalStakeUSDValue;
        const p = this.pool.lendContract
          ? getContract(this.pool.lendContract, ibTokenABI)
          : {};
        const FTMUSDCVault = getContract(stakePools[8].lendContract, vaultABI);
        const FTMUSDClp = getContract(await FTMUSDCVault.wantToken(), lpABI);
        const ftmUsdcReserves = await FTMUSDClp.getReserves();
        const ftmPrice = new BigNumber(ftmUsdcReserves[0].toString())
          .dividedBy(1e6)
          .dividedBy(
            new BigNumber(ftmUsdcReserves[1].toString()).dividedBy(1e18)
          );
        switch (this.pool.id) {
          case 0:
            var spoon = new ethers.Contract(akitaAddress, ERC20, getProvider());
            var spoonlpBalance = new BigNumber(
              (await spoon.balanceOf(this.poolInfo.lpToken)).toString()
            ).multipliedBy(2);
            totalStakeUSDValue = spoonlpBalance
              .multipliedBy(
                (await this.stakeToken.balanceOf(fairLaunchAddress)).toString()
              )
              .dividedBy((await this.stakeToken.totalSupply()).toString())
              .multipliedBy(this.tokenPrice)
              .dividedBy(new BigNumber(10).pow(18));
            break;
          case 1:
            totalStakeUSDValue = new BigNumber(
              (await p.totalSupply()).toString()
            )
              .div(new BigNumber(10).pow(18))
              .multipliedBy(this.tokenPrice);
            break;
          case 2:
            totalStakeUSDValue = new BigNumber(
              (await p.totalSupply()).toString()
            )
              .div(new BigNumber(10).pow(this.pool.decimals))
              .multipliedBy(
                await this.getTokenPrice(
                  [await p.want(), usdAddress],
                  18,
                  usdDecimals
                )
              );
            break;
          case 3:
            totalStakeUSDValue = new BigNumber(
              (await p.totalSupply()).toString()
            ).div(new BigNumber(10).pow(this.pool.decimals));
            break;
          case 4:
            totalStakeUSDValue = new BigNumber(
              (await p.totalSupply()).toString()
            ).div(new BigNumber(10).pow(this.pool.decimals));
            break;
          case 5:
            totalStakeUSDValue = new BigNumber(
              (await p.totalSupply()).toString()
            ).div(new BigNumber(10).pow(this.pool.decimals));
            break;
          case 6:
          case 7:
          case 8:
          case 9:
          case 10:
            var pool = getContract(this.pool.lendContract, vaultABI);
            var lp = getContract(await pool.wantToken(), lpABI);
            // 取 lp 余额
            var reserves = await lp.getReserves();
            totalStakeUSDValue = ftmPrice
              .multipliedBy(
                new BigNumber(
                  reserves[
                    (await lp.token0()).toString().toLowerCase() ===
                    wftmAddress.toLowerCase()
                      ? 0
                      : 1
                  ].toString(10)
                )
                  .multipliedBy((await pool.totalTokenBalance()).toString(10))
                  .dividedBy((await lp.totalSupply()).toString(10))
                  .dividedBy(1e18)
              )
              .multipliedBy(2)
              .multipliedBy(
                (await pool.balanceOf(fairLaunchAddress)).toString()
              )
              .dividedBy((await pool.totalSupply()).toString());
            break;
        }

        // console.log(
        //   "bingo",
        //   this.pool.id,
        //   ftmPrice.toString(10),
        //   totalStakeUSDValue.toString(10)
        // );

        this.apy = yearlyUSDReward
          .multipliedBy(100)
          .dividedBy(totalStakeUSDValue);
      } catch (error) {
        console.log("calcAPY", this.pool.id, error);
      }
    },
    async loadData() {
      this.poolInfo = await getFairLaunchContract().poolInfo(this.pool.id);
      const signer = getProvider().getSigner();
      this.stakeToken = new ethers.Contract(
        this.poolInfo.lpToken,
        ERC20,
        getProvider()
      ).connect(signer);
      this.calcAPY();
      this.needApprove = (
        await this.stakeToken.allowance(
          this.$store.state.account,
          fairLaunchAddress
        )
      ).lt(ethers.utils.parseUnits("10000000000", this.pool.decimals));
      this.balance = ethers.utils.formatUnits(
        await this.stakeToken.balanceOf(this.$store.state.account),
        this.pool.decimals
      );
      this.staked = ethers.utils.formatUnits(
        (
          await getFairLaunchContract().userInfo(
            this.pool.id,
            this.$store.state.account
          )
        ).amount,
        this.pool.decimals
      );
      this.rewards = await getFairLaunchContract().pendingReward(
        this.pool.id,
        this.$store.state.account
      );
    },
  },
};
</script>

<style></style>
