import { BigNumber } from "bignumber.js";

// 不使用四舍五入
BigNumber.set({ ROUNDING_MODE: BigNumber.ROUND_FLOOR });

const dealWithZeros = function(str) {
  str = str.replace(/^0+|0+$/g, "");
  if (str === ".") {
    return "0";
  } else if (str.startsWith(".")) {
    return "0" + str;
  } else if (str.endsWith(".")) {
    return str.substr(0, str.length - 1);
  } else {
    return str;
  }
};

const isZero = function(val) {
  if (!val || val === "0") {
    return true;
  }
  if (val instanceof BigNumber) {
    return val.isZero();
  }
  return new BigNumber(val.toString()).isZero();
};

const formatBN = function(src, data) {
  if (!src) {
    return "0";
  }
  return dealWithZeros(
    new BigNumber(src.toString())
      .div(
        new BigNumber(10).pow(
          data?.decimals === undefined ? 18 : data?.decimals
        )
      )
      .dp(data?.dp || 8)
      .toFormat(data?.dp || 8)
  );
};

const mulBN = function(bnList) {
  if (!bnList) {
    return "0";
  }
  let bn = new BigNumber(1);
  for (let i = 0; i < bnList.length; i++) {
    bn = bn.multipliedBy(bnList[i]);
  }
  return bn;
};

const formatBNWithoutGroup = function(src, data) {
  return formatBN(src, data).replaceAll(",", "");
};

export default function initVue(app) {
  app.config.globalProperties.$filters = {
    mulBN,
    isZero,
    formatBN,
    formatBNWithoutGroup,
    ...app.config.globalProperties.$filters,
  };

  app.mixin({
    methods: {
      mulBN,
      isZero,
      formatBN,
      formatBNWithoutGroup,
    },
  });
}
